*,
*::before,
*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
body{
  background-color: rgb(255, 255, 255);

}
html{
  font-size: 62.5%;
}
.cards{
  width: 100vw;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: row;
  /* place-items: center; */
  flex-wrap: wrap;
  justify-content:space-evenly;
}
.card{
  width: 32rem;
  height: 43rem;
  background-color: rgb(255, 255, 255);
  border-radius: 0.6rem;
  box-shadow: 0 1.2rem 3rem 0.5rem rgb(49, 49, 49);
  /* padding: 1rem; */
  text-align: center;
  margin: 3rem;
}
img{
  width: 100%;
  height: 33rem;
  object-fit: cover;
  /* box-shadow: 0 1.2rem 3rem 0.5rem rgb(143, 143, 143) ; */
  border-radius: 2%;
}
span{
  color: rgb(255, 0, 0);
  font-size: 1.8rem;
  text-transform: capitalize;
  margin: 0.5rem;
}
h3{
  font-size: 1.5rem;
}
button{
  padding: 5px 8px;
  font-family: "Raleway",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  border-radius: 2px;


 
  
}
button:hover{
  background-color: grey;
  color: white;
}
h1{
  text-align: center;
  font-size: 3rem;
  color: red;
  /* border: 2px solid black; */
  /* background-color: rgb(0, 0, 0); */
  padding-bottom: 1rem;
  padding-top: 1rem;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  text-transform: uppercase;
  font-family: "Raleway",sans-serif;
  
}

.card:hover{
  box-shadow: 0 1.2rem 3rem 0.5rem rgb(49, 49, 49);
  transform: scale(1.05,1.05);

}
.card_info{
  /* background-color: #fff; */
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  /* background-color: #fff; */
}
.card_category{
  /* font-family: "Raleway"sans-serif; */
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #ff0000;
  /* background-color: #fff; */

}

.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway",sans-serif;
  /* background-color: #fff; */
  text-transform: capitalize;
}


